/// <reference path="./typings/index.d.ts"/>
"use strict";
var http = require("http");
var express = require("express");
var bodyparser = require("body-parser");
var cookieparser = require("cookie-parser");
var authentication_route_1 = require("./web/routes/authentication-route");
var product_route_1 = require("./web/routes/product-route");
var web_shop_route_1 = require("./web/routes/web-shop-route");
var app = express();
app.use(cookieparser());
app.use("/api", bodyparser.json());
app.use("/api", authentication_route_1.default);
app.use("/api/products", product_route_1.default);
app.use("/api/shops", web_shop_route_1.default);
app.use("/", express.static("./out/client"));
app.use("/", function (req, res) {
    res.sendfile("./out/client" + '/index.html');
});
var port = process.env.OPENSHIFT_NODEJS_PORT || 8081;
var ipAddress = process.env.OPENSHIFT_NODEJS_IP || '127.0.0.1';
function run() {
    var server = http.createServer(app);
    server.listen(port, ipAddress, function () {
        console.info("Web server started at " + ipAddress + ":" + port + "/");
    });
    return server;
}
exports.run = run;



